import React, { useState } from 'react';
import { useHomeWork } from '../../../../hooks/useHomeWork';
import HomeworkWorkingPart from './Component/Homework-workingPart/Homework-workingPart';
import HomeWorkItem from './Component/HomeWordItem/HomeWorkItem';
import "./HomeWork.scss";

const HomeWork: React.FC = () => {
  const homework = useHomeWork();

  const [activeHomeWork, setActiveHomeWork] = useState<string>("");

  const homeworkList = homework?.map((homeworkItem) => {
    return (
      <div onClick={() => setActiveHomeWork(homeworkItem.title)}><HomeWorkItem homework={homeworkItem} active={activeHomeWork} /></div>
    )
  });

  const foundedActiveHomework = homework.find((item: any) => item.title === activeHomeWork);

  return (
    <div className="homework">
      <div className="homework-list col-12 col-lg-5 h-100"> 
        { homeworkList }
      </div>
      <div className="homework-part col-12 col-lg-7 h-100">
        <HomeworkWorkingPart homework={foundedActiveHomework}/>
      </div>
    </div>
  )
};

export default HomeWork;
